import React from 'react';
import {
    Button,
    Icon,
} from 'antd';
import {Gap} from '../index';

/**
 * 
 * @param props
 *          isShow 
 *          changeShowState: 
 *          copyCode: 
 * @returns {*}
 * @constructor
 */
function Index({
   isShow = false,
   changeShowState,
   copyCode,
   ...props}) {
    return (
        <div style={{display: 'flex'}}>
            {/*isShowtrue*/}
            {/**/}
            <div onClick={changeShowState}>
                {isShow ? (
                    <Button icon={'eye-invisible'}></Button>
                ) : (<Button icon={'eye'}></Button>)}
            </div>
            <Gap margin={[0, '24px', 0, 0]} />
            <Button icon={'copy'} onClick={copyCode}></Button>
        </div>
    );
}

export default Index;



